@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
body h1, body h2 {
  margin-bottom: 1vh;
}
body > main {
  padding-top: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
body > main > .part1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 105%;
  justify-content: center;
  background-image: linear-gradient(white, lightblue, white);
  grid-gap: 10px 10px;
}
body > main > .part1 > .img-container > .img {
  -webkit-box-shadow: inset 0px 0px 5px 3px rgb(145, 145, 145), 0px 0px 5px 3px rgb(145, 145, 145);
  -moz-box-shadow: inset 0px 0px 5px 3px rgb(145, 145, 145), 0px 0px 5px 3px rgb(145, 145, 145);
  box-shadow: inset 0px 0px 5px 3px rgb(145, 145, 145), 0px 0px 5px 3px rgb(145, 145, 145);
  border-radius: 10px;
}

@media only screen and (min-width: 801px) {
  body > main > .part1 {
    flex-direction: row;
  }
  body > main > .part1 > .img-container {
    width: 40%;
    height: 40%;
    display: flex;
    justify-content: center;
  }
  body > main > .part1 > .content-container {
    margin-inline: 1vw;
    width: 40%;
    height: 40%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  body > main > .part1 > .content-container > p {
    font-size: 1rem;
    margin-bottom: 2vh;
  }
}
@media only screen and (max-width: 800px) {
  body > main {
    grid-gap: 10px 10px;
  }
  body > main > .part1 {
    flex-direction: column;
  }
  body > main > .part1 > .img-container {
    display: flex;
    justify-content: center;
  }
  body > main > .part1 > .img-container > img {
    width: 30%;
    height: 30%;
  }
  body > main > .part1 > .content-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
    margin-inline: 20px;
  }
  body > main > .part1 > .content-container > .center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.overl {
  position: absolute;
  top: 0;
  left: 0;
}

.o-active {
  width: 100vw;
  height: 100vh;
  background: white;
  pointer-events: all;
  opacity: 0.5;
  z-index: 1;
}

button {
  color: rgb(77, 77, 77);
  cursor: pointer;
}

#button {
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: #7abfff;
  width: 120px;
  height: 50px;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  border: 3px outset gray;
  -webkit-box-shadow: 1px 1px 2px 1px gray;
  box-shadow: 1px 1px 2px 1px black;
}
#button:hover {
  color: black;
  transform: scale(1.05);
  background-color: #56adff;
}
#button:active {
  color: #8b0000;
  background: white;
  transform: scale(1.1);
}

.btnX {
  font-size: 1.5rem;
  outline: none;
  border: none;
  background: none;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.reset {
  visibility: hidden;
  -webkit-transform: scale(0);
  transform: scale(0);
  border-radius: 32px;
  font-weight: 700;
  font-size: 1rem;
  background: #7abfff;
  width: auto;
  height: 50px;
  transition: transform 1s;
  padding-block: auto;
  -webkit-box-shadow: 1px 1px 2px 1px gray;
  box-shadow: 1px 1px 2px 1px gray;
  -webkit-transform: scale(1);
  margin-left: 3vw;
  border: 3px outset gray;
}

.r-active {
  visibility: visible;
  transform: scale(1);
}
.r-active:hover {
  color: black;
  transform: scale(1.05);
  background-color: #56adff;
}
.r-active:active {
  color: #8b0000;
  background: white;
  transform: scale(1.1);
}

.popup {
  visibility: hidden;
  width: 40vw;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  -webkit-box-shadow: 0px 0px 7px 1px gray;
  box-shadow: 0px 0px 7px 1px gray;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: left;
  -ms-flex-align: left;
  align-items: left;
  line-height: 1.6;
  padding: 2% 5% 2% 5%;
}
.popup > h2 {
  font-size: 2rem;
}
.popup > p {
  font-size: 1rem;
}
.popup > p > span {
  color: #8b0000;
  font-size: 1.5rem;
  font-weight: 700;
}

.active {
  visibility: visible !important;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
}

html {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

main {
  background-color: #FFFFFF;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-items: center;
  align-items: center;
}

#intro {
  display: flex;
  background-color: rgb(230, 249, 255);
  align-items: center;
  margin-inline: 5%;
  -webkit-box-shadow: inset 0px 0px 5px 1px rgb(165, 165, 165), 0px 0px 5px 1px rgb(165, 165, 165);
  -moz-box-shadow: inset 0px 0px 5px 1px rgb(165, 165, 165), 0px 0px 5px 1px rgb(165, 165, 165);
  box-shadow: inset 0px 0px 5px 1px rgb(165, 165, 165), 0px 0px 5px 1px rgb(165, 165, 165);
  border: 1px solid gray;
  border-radius: 15px;
}
#intro > .center {
  display: flex;
  flex-direction: column;
  text-align: justify;
  margin-inline: 20px;
}
#intro > .center h1 {
  text-align: center;
}
#intro > .center p {
  font-style: italic;
  margin-inline: 3%;
  margin-bottom: 3%;
}

.hr {
  margin-block: 20px;
  size: 1;
  width: 80%;
  color: "gray";
}